App.FilesAlertController = App.ErrorController.extend({
  content:null,
  output:function () {
    var error = this.get('content'),
        message = (error.responseJSON)?error.responseJSON.message:error.message,
        output;

    if (message) {
      message = message.split('\n').objectAt(0);
    }

    return message;
  }.property('content')
});
